@import "./vars.scss";

/* roboto-regular - latin */
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  src: url("./fonts/roboto-v29-latin-regular.eot"); /* IE9 Compat Modes */
  src: local(""), url("./fonts/roboto-v29-latin-regular.eot?#iefix") format("embedded-opentype"),
    /* IE6-IE8 */ url("./fonts/roboto-v29-latin-regular.woff2") format("woff2"),
    /* Super Modern Browsers */ url("./fonts/roboto-v29-latin-regular.woff") format("woff"),
    /* Modern Browsers */ url("./fonts/roboto-v29-latin-regular.ttf") format("truetype"),
    /* Safari, Android, iOS */ url("./fonts/roboto-v29-latin-regular.svg#Roboto") format("svg"); /* Legacy iOS */
}
/* roboto-500 - latin */
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  src: url("./fonts/roboto-v29-latin-500.eot"); /* IE9 Compat Modes */
  src: local(""), url("./fonts/roboto-v29-latin-500.eot?#iefix") format("embedded-opentype"),
    /* IE6-IE8 */ url("./fonts/roboto-v29-latin-500.woff2") format("woff2"),
    /* Super Modern Browsers */ url("./fonts/roboto-v29-latin-500.woff") format("woff"),
    /* Modern Browsers */ url("./fonts/roboto-v29-latin-500.ttf") format("truetype"),
    /* Safari, Android, iOS */ url("./fonts/roboto-v29-latin-500.svg#Roboto") format("svg"); /* Legacy iOS */
}

body,
* {
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif;
  box-sizing: border-box;
  font-size: 16px;

  @media screen and (max-width: 420px) {
    font-size: 12px;
  }
}

body .body--no-overflow {
  overflow: hidden;
}
